@import '../../index.scss';

.gpt3 {
  &__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;

    margin: 1rem;
  }

  &__features-container__feature-title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;

    h1 {
      font-family: $fontFamily;
      font-weight: 800;
      font-size: 18px;
      line-height: 24px;
      letter-spacing: -0.04em;
      color: #ffffff;
    }

    div {
      width: 38px;
      height: 3px;
      background: $gradientBar;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
      margin-bottom: 0.25rem;
    }
  }

  &__features-container__feature-text {
    flex: 2;
    max-width: 390px;
    display: flex;

    p {
      font-family: $fontFamily;
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
      color: $colorText;
    }
  }
}

@media screen and (max-width: 550px) {
  .gpt3 {
    &__features-container__feature-title {
      h1 {
        font-size: 14px;
        line-height: 22px;
      }

      p {
        font-size: 12px;
        line-height: 20px;
      }
    }
    &__features-container__feature {
      margin: 1rem 0;
    }
  }
}
