@import '../../index.scss';

.gpt3 {
  &__cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: $gradientBar;
  }

  &__cta-content {
    p {
      font-family: $fontFamily;
      font-weight: 500;
      font-size: 12px;
      line-height: 30px;
      color: #0e0e0e;
    }

    h3 {
      font-family: $fontFamily;
      font-weight: 800;
      font-size: 24px;
      line-height: 45px;
      color: #000000;
    }
  }

  &__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;

    button {
      background: #000000;
      border-radius: 40px;

      font-family: $fontFamily;
      font-weight: bold;
      font-size: 18px;
      line-height: 30px;
      color: #ffffff;
      padding: 0.5rem 1rem;

      border: none;
      outline: none;
      cursor: pointer;
      min-width: 150px;
    }
  }
}

@media screen and (max-width: 650px) {
  .gpt3 {
    &__cta {
      flex-direction: column;
    }

    &__cta-btn {
      margin: 2rem 0 0;
    }
  }
}

@media screen and (max-width: 550px) {
  .gpt3 {
    &__cta {
      margin: 2rem 4rem;
    }

    &__cta-content {
      h3 {
        font-size: 18px;
        line-height: 32px;
      }

      p {
        line-height: 22px;
        margin-bottom: 1rem;
      }
    }

    &__cta-btn {
      button {
        font-size: 14px;
        line-height: 20px;
      }
    }
  }
}
